/** @jsx jsx */
import React from 'react'
import { jsx, Styled } from 'theme-ui'
import { Grid } from '@theme-ui/components'
import Card from './card'
import {
  Container,
  Box,
  Flex,
  Text,
  Image,
  Link as StyledLink,
} from '@theme-ui/components'
import { useStaticQuery, graphql } from 'gatsby'
import Link from './link'
import SEO from './seo'
import Header from './header'
import { MDXProvider } from '@mdx-js/react'
import ResponsiveEmbed from 'react-responsive-embed'
import { TwitterTweetEmbed } from 'react-twitter-embed'
import TableOfContents from '../components/toc'
import { ProSidebar, SidebarHeader, SidebarContent, Menu, MenuItem, SubMenu } from 'react-pro-sidebar';
import '../styles/prosidebar.css';
import { FaToolbox, FaNewspaper,FaRegMap,FaOsi ,FaCarCrash } from 'react-icons/fa';
import { GiMovementSensor, GrGlobe  } from 'react-icons/gi';
import { SiToyota, SiHyundai, SiMercedes, SiBmw, SiVolkswagen, SiTesla, SiHonda, SiRenault, SiFord,SiSkyliner,SiReadthedocs } from 'react-icons/si';

export default ({
  children,
  title,
  image,
  description,
  timeToRead,
  categories,
  github,
  slug,
  showToc,
  tocData,
}) => {
  const [isToggled, setToggled] = React.useState(false)
  const data = useStaticQuery(graphql`
    query ArticleTemplateQuery {
      site {
        siteMetadata {
          title
        }
      }
    }
  `)
  return (
    <Styled.root>

      <div sx={{ minHeight: '100vh', display: 'flex', flexDirection: 'row', flexGrow: 'auto', justifyContent: 'space-between'}}> 

        <SEO
          title={title || data.site.siteMetadata.title}
          image={image}
          description={description}
          timeToRead={timeToRead}
          categories={categories}
        />
        <aside sx={{
          marginRight: '5vh',
          position: 'static',
          display: ['none', 'none', 'block'],
        }}>
          <ProSidebar>
            <SidebarHeader>
              <div sx={{
                marginLeft: '1vh',
                position: 'relative',
                display: ['none', 'none', 'block'],
              }}>
                <h4 sx={{
                  margin: '1vh',
                }}></h4>
                <h3 sx={{
                  margin: '1vh',
                }}>ADAS Engineer</h3>
              </div>

              
            </SidebarHeader>
            <Menu>
              <MenuItem icon={<SiSkyliner/>} > <a href="/">Overview</a></MenuItem>
              <SubMenu title="Toolbox" defaultOpen='true' icon={<FaToolbox />}>
                <MenuItem ><a href="/toolbox/ecu-com"> ECU Communication</a></MenuItem>
                <MenuItem> <a href="/toolbox/image-pro">Image Processing</a></MenuItem>
                <MenuItem> <a href="/toolbox/emb-sys">Embedded Systems </a></MenuItem>
                <MenuItem> <a href="/toolbox/hil-sim"> X in Loop / Simulation</a></MenuItem>
                <MenuItem><a href="/toolbox/real-equ">Real Scenario Test Equipments</a></MenuItem>
              </SubMenu>
              <SubMenu title="Sensors" defaultOpen='true' icon={<GiMovementSensor  />}>
                <MenuItem><a href="/sensors/radar">Radar</a></MenuItem>
                <MenuItem><a href="/sensors/camera">Camera</a></MenuItem>
                <MenuItem><a href="/sensors/lidar">Lidar</a></MenuItem>
                <MenuItem  ><a href="/sensors/gps">GPS</a></MenuItem>
                <MenuItem><a href="/sensors/imu">IMU</a></MenuItem>
                <SubMenu title="Sensor Fusion" defaultOpen='true'>
                  <MenuItem><a href="/sensors/adasis">ADASIS ( ADAS Maps ) </a></MenuItem>
                  <MenuItem  ><a href="/sensors/sensoris">SENSORIS</a></MenuItem>
                  <MenuItem><a href="/sensors/vtox">V2X Technology</a></MenuItem>
                </SubMenu>
              </SubMenu>
            </Menu>
          </ProSidebar>
        </aside>
        <div sx={{ 
          maxWidth: ['90ch', '90ch', showToc ? 1800 : '90ch'],
          width: '100%',
          minHeight: '100vh',
          display: 'flex', 
          alignSelf: 'stretch', 
          flexDirection: 'column', 
          flexGrow: 'auto', 
          alignItems: 'stretch'
           }}>
          <Header showToc={showToc} />
          <div
            sx={{
              mt: [60, 20, 0],
              display: 'grid',
              gridTemplateColumns: [
                'auto',
                'auto',
                showToc ? '8fr 2.5fr' : '100%',
              ],
              maxWidth: ['90ch', '90ch', showToc ? 1800 : '90ch'],
              width: '100%',
              mx: 'auto',
            }}
          >
            <main sx={{ minWidth: 0, flex: '1 1 auto' }}>
              <Container>
                <MDXProvider
                  components={{
                    h1: props => <h1 {...props} style={{ marginTop: '12px',  marginBottom: '12px',color:'gray' }} />,
                    h2: props => <h2 {...props} style={{ marginTop: '12px',  marginBottom: '12px',color:'gray' }} />,
                    h3: props => <h3 {...props} style={{ marginTop: '12px',  marginBottom: '12px',paddingLeft:'12px',color:'gray' }} />,
                    li: props => <li {...props} style={{ marginTop: '12px',  marginBottom: '12px',color:'gray' }} />,
                    p: props => <p {...props} style={{ margin: '0px', textAlign:'justify' }} />,
                    TwitterTweetEmbed,
                    ResponsiveEmbed,
                    Box,
                    Card,
                    Text,
                    Grid,
                    Flex,
                    Image,
                    Link,
                    StyledLink,
                    jsx,
                  }}
                >
                  {children}
                </MDXProvider>
              </Container>
            </main>
            {showToc && (
              <aside
                sx={{
                  width: '100%',
                  position: 'relative',
                  display: ['none', 'none', 'block'],
                }}
              >
                <TableOfContents
                  data={tocData}
                  slug={slug}
                  sx={{ position: 'sticky', top: 0 }}
                />

              </aside>
            )}
          </div>

          <footer
            sx={{
              maxWidth: ['80ch', '80ch', showToc ? 1200 : '80ch'],
              mx: 'auto',
              py: [4, 5],
            }}
          >
          </footer>
        </div>
               
        <aside sx={{
          marginLeft: '5vh',
          position: 'static',
          display: ['none', 'none', 'block'],
        }}>
          <ProSidebar  >
            <SidebarHeader > 
              <div sx={{
                marginLeft: '1vh',
                position: 'relative',
                display: ['none', 'none', 'block'],
              }}>
                <h4 sx={{
                  margin: '2vh',
                }}> Design Stage V.004  </h4>
                <h4  sx={{
                  margin: '2vh',
                }}>   </h4>
              </div>

            </SidebarHeader>
                
            <div sx={{ display: 'flex', flexDirection: 'row', justifyContent: 'space-around' }}>

                  <Card href='/oems/mercedes' variant='logolike'>
                    <SiMercedes />
                  </Card>
                  <Card href='/oems/bmw' variant='logolike'>
                    <SiBmw />
                  </Card>
                  <Card href='/oems/ford' variant='logolike'>
                    <SiFord />
                  </Card>
                  <Card href='/oems/volkswagen' variant='logolike'>
                    <SiVolkswagen />
                  </Card>
                  <Card href='/oems/tesla' variant='logolike'>
                    <SiTesla  />
                  </Card>
                  <Card href='/oems/renault' variant='logolike'>
                    <SiRenault />
                  </Card>
                  <Card href='/oems/toyota' variant='logolike'>
                    <SiToyota />
                  </Card>
                  <Card href='/oems/hyundai' variant='logolike'>
                    <SiHyundai />
                  </Card>
                  <Card href='/oems/honda' variant='logolike'>
                    <SiHonda />
                  </Card>
                </div>

            <Menu>
                {/* <div sx={{ display: 'flex', flexDirection: 'row', justifyContent: 'space-around' }}>
                  
                 <a href="/oems/mercedes">    <SiMercedes color="white" size="1em"/>  </a>
                 <a href="/oems/hyundai">    <SiHyundai color="white" size="1em"/>  </a>
                 <a href="/oems/ford">       <SiFord color="white" size="1em"/> </a>
                 <a href="/oems/toyota">     <SiToyota size="1em" /> </a>
                 <a href="/oems/volkswagen"> <SiVolkswagen color="white" size="1em"/> </a>
                 <a href="/oems/tesla">      <SiTesla color="white" size="1em"/> </a>
                 <a href="/oems/renault">    <SiRenault color="white" size="1em"/></a>
                 <a href="/oems/bmw">        <SiBmw color="white" size="1em"/></a>
                 <a href="/oems/honda">        <SiHonda color="white" size="1em"/></a>
                  
                </div> */}
              <SubMenu title="Euro-NCAP" defaultOpen='true' icon={<FaCarCrash/>}  >
                <MenuItem><a href="/ncap/ncap-overview">NCAP-Overview</a></MenuItem>
                <MenuItem><a href="/ncap/aeb">AEB -  Emergeny Braking</a></MenuItem>
                <MenuItem><a href="/ncap/sas">SAS - Speed Assist Systems</a></MenuItem>
                <MenuItem><a href="/ncap/lss">LSS - Lane Suport Systems</a></MenuItem>
                <MenuItem><a href="/ncap/dms">Driver Monitoring</a></MenuItem>
              </SubMenu>
              <SubMenu title="OpenSource" defaultOpen='true'  icon={<FaOsi />}>
                <MenuItem><a href="/">Rviz-3D</a></MenuItem>
                <MenuItem><a href="/">AVS</a></MenuItem>
                <MenuItem><a href="/">Carla-Simulator for AD</a></MenuItem>
                <MenuItem><a href="/">BMW Innovation Lab</a></MenuItem>
              </SubMenu> 
              <SubMenu title="Standards" defaultOpen='true' icon={<SiReadthedocs/>}  >
                <MenuItem><a href="/">ISO 26262</a></MenuItem>
                <MenuItem><a href="/"></a></MenuItem>
                <MenuItem><a href="/"></a></MenuItem>
                <MenuItem><a href="/"></a></MenuItem>
              </SubMenu>
              <SubMenu title="About Me" defaultOpen='true' icon={<SiReadthedocs/>}  >
                <MenuItem><a href="http://fuzmez.space/">fuzmez</a></MenuItem>
                 
              </SubMenu>
            </Menu>
          </ProSidebar>
        </aside> 
     
      </div > 

      
     
    </Styled.root>
  )
}
